html {
  font-family: "latolight";
  color: #414141;
  font-size: 16px; }

body {
  width: 100%;
  background-color: #f6f6f6;
  padding-top: 61px; }

body.loaded {
  opacity: 1;
  transition: opacity .34s ease; }

body.closing {
  transform: translateY(-100px);
  opacity: 0;
  transition: opacity .34s ease, transform .34s ease; }

h1, h2 {
  font-family: "Avenir Bold"; }

.container {
  max-width: 900px;
  margin: 0 auto;
  padding-left: 1rem;
  padding-right: 1rem; }

